import '../assets/css/Contact.scss';

export default function Contact() {
    return (
        <section id="contact">
          <h2>Contact</h2>

          <form action="https://api.web3forms.com/submit" method="POST">
            <input type="hidden" name="access_key" value="5ecd38ca-833f-4ae9-849d-6a8148791d8d" />
            <input type="text" name="name" placeholder="Name" minLength={3} maxLength={50} />
            <input type="email" name="email" placeholder="E-mail" minLength={3} maxLength={50} />
            <input type="text" name="topic" placeholder="Topic" minLength={3} />
            <textarea name="message" placeholder="Message" minLength={3} />
            <input type="submit" value="Send" />
          </form>

      </section>
    )
}